import html2canvas from 'html2canvas';
import { jsPDF } from 'jspdf';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service'
import { File } from '@ionic-native/file/ngx';
import { TranslateService } from '@ngx-translate/core';
import { FileOpener } from '@awesome-cordova-plugins/file-opener/ngx';
import { AlertController, ToastController } from  '@ionic/angular';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { AndroidPermissions } from '@awesome-cordova-plugins/android-permissions/ngx';
import { EMedicalCardService } from "../e-medical-card/eMedicalCardService";

@Component({
  selector: 'app-download-medicard',
  templateUrl: './download-medicard.page.html',
  styleUrls: ['./download-medicard.page.scss'],
})
export class DownloadMedicardPage implements OnInit {

  isFlipped: boolean = false;
  dirName = 'SunAccess';
  containerLogo: any;
  containerFront: any;
  containerBack: any;
  containerPdf: any;
  containerImage: any;
  imgData: any;
  policyDetails;
  //  {
  //   "policyNo": "52728292",
  //   "insuredName": "TAN JONG YAN",
  //   "identityNo": "700102140102",
  //   "productName": "SunLink Max",
  //   "validFrom": "1/11/2021",
  //   "policyStatus": "InForce",
  //   "planStatus": "InForce",
  //   "medicalCardStatus": "valid",
  //   // "medicalCardStatus": "notinforce"
  // };

  containerId: any;
  errorMsg: any;
  numberLabels;

  constructor(

   
    private config: ConfigProvider,
    public file: File,
    public utils: UtilService,
    public translate: TranslateService,
    public fileOpener: FileOpener, public alertCtrl: AlertController,
    public toastCtrl: ToastController, public logger: LoggerServiceProvider,
    private androidPermissions: AndroidPermissions,
    public eMedicalCardSrvc: EMedicalCardService) { }

  ngOnInit() {
    this.policyDetails =  history.state.policyDetails;
    console.log('policyDetails', this.policyDetails)
    this.policyDetails['identityNo'] = this.policyDetails['identityNo'].replace(/(\d{6})(\d{2})(\d{4})/, "$1-$2-$3");
    this.policyDetails['validFrom'] = this.policyDetails['validFrom'].split(" ")[0];
    
  }
  // ionViewDidLoad() {
  //   this.policyDetails = this.navParams.data;
  //   this.policyDetails['identityNo'] = this.policyDetails['identityNo'].replace(/(\d{6})(\d{2})(\d{4})/, "$1-$2-$3");
  //   this.policyDetails['validFrom'] = this.policyDetails['validFrom'].split(" ")[0];
  //   console.log('policyDetails', this.policyDetails)
  //   console.log("ionViewDidLoad DownloadMedicardPage");
  // }

  flipCard() {
    this.isFlipped = !this.isFlipped;
    console.log(this.isFlipped);
  }

  downloadDoc(fileMimeType, containerId) {
    this.utils.showProgressLoader();
    this.containerId = containerId;
    this.containerPdf = document.getElementById(containerId);

    html2canvas(this.containerPdf, {
      onclone: function (clonedDoc) {
        clonedDoc.getElementById('download-container').style.display = 'block';
      }
    }).then((canvas) => {
      console.log(canvas);
      this.imgData = canvas.toDataURL("image/jpeg");
      var blobPdf:any;
      var fileName;
      if (fileMimeType === 'application/pdf') {
       var doc = new jsPDF();
       var imageHeight = canvas.height * 180 / canvas.width;
       doc.addImage(this.imgData, 10, 10, 180, imageHeight);
       this.imgData = doc.output('blob');
       console.log('this.imgData',this.imgData)
         blobPdf = this.imgData;
        fileName = 'medicard.pdf';
      } else if (fileMimeType === 'image/jpeg') {
        this.imgData = this.imgData.replace(/^data:image\/(jpeg);base64,/, "");
        let sliceSize = 512;
        var byteCharacters = atob(this.imgData);
        var byteArrays = [];
        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
          var slice = byteCharacters.slice(offset, offset + sliceSize);
          var byteNumbers = new Array(slice.length);
          for (var i = 0; i < slice.length; i++) {
            byteNumbers[i] = slice.charCodeAt(i);
          }
          var byteArray = new Uint8Array(byteNumbers);
          byteArrays.push(byteArray);
        }
        blobPdf = new Blob(byteArrays, { type: fileMimeType });
        fileName = 'medicard.jpeg';
      }

      if (this.utils.getPlatform() == 'ios') {
        this.file.writeFile(this.file.documentsDirectory, fileName, blobPdf, { replace: true })
          .then(res => {
            this.openFile(this.file.documentsDirectory + "/" + fileName, fileMimeType);
          })
          .catch(err => {
            this.utils.dissmisProgressLoader();
            this.presentToast(this.translate.instant('generic_msgs.err_download_file'));
          });
      } else if (this.utils.getPlatform() == 'android') {
        console.log('blobPdf',blobPdf)
        this.file.checkDir(this.file.externalDataDirectory, this.dirName)
          .then(res => {
            this.file.checkFile(this.file.externalDataDirectory + this.dirName + '/', fileName)
              .then(res => {
                this.utils.dissmisProgressLoader();
                if (fileMimeType != 'application/zip') {
                  this.showDialog(fileName, "", this.translate.instant('generic_msgs.file_alredy_downloaded'), fileMimeType);
                } else {
                  this.presentToast(this.translate.instant('save-photo.file_exists') + this.file.externalDataDirectory + this.dirName);
                }
              })
              .catch(err => {
                this.file.writeFile(this.file.externalDataDirectory + this.dirName + '/', fileName, blobPdf, { replace: true })
                  .then(res => {
                    this.saveFile(fileMimeType, res.toInternalURL());
                  })
                  .catch(err => {
                    this.utils.dissmisProgressLoader();
                    this.presentToast(this.translate.instant('generic_msgs.err_download_file'));
                  });
              });

          })
          .catch(err => {
            this.file.createDir(this.file.externalDataDirectory, this.dirName, false)
              .then((res) => {
                this.file.writeFile(this.file.externalDataDirectory + this.dirName + '/', fileName, blobPdf, { replace: true })
                  .then(res => {
                    this.saveFile(fileMimeType, res.toInternalURL());
                  })
                  .catch(err => {
                    this.utils.dissmisProgressLoader();
                    this.presentToast(this.translate.instant('generic_msgs.err_download_file'));
                  });
              })
              .catch(err => {
                this.utils.dissmisProgressLoader();
                this.presentToast(this.translate.instant('generic_msgs.err_download_file'));
              });
          });
      }
    });
  }

  showDialog(name, title: string, message: string, fileType?: string, downloadLink?: string) {
    let buttons = [{
      text: this.translate.instant('save-photo.btn_open'),
      handler: () => {
        this.logger.trace("existing " + this.file.externalDataDirectory + this.dirName + '/' + name);
        this.checkPermission(this.file.externalDataDirectory + this.dirName + '/' + name, fileType);
      }
    }, {
      text: this.translate.instant('save-photo.download_again'),
      handler: () => {
        this.file.removeFile(this.file.externalDataDirectory + this.dirName + '/', name).then(res => {
          this.presentToast(this.translate.instant('save-photo.downlaod_progress'));
          this.downloadDoc(fileType, this.containerId);
        }).catch(err => {
          this.presentToast(this.translate.instant('generic_msgs.err_download_file'));
        });
      }
    }, {
      text: this.translate.instant('save-photo.btn_cancel'),
      handler: () => {
      }
    }];

    let alert = this.alertCtrl.create({
      header: title,
      message: message,
      buttons: buttons,
      backdropDismiss: false,
    }).then((alert) => alert.present());

  }



  async presentToast(msg) {
    const toast = await this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom',
      
    });

    await toast.present();
  }

  checkPermission(location, fileMimeType) {
    this.androidPermissions.hasPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
      .then(status => {
        if (status.hasPermission) {
          this.openFile(location, fileMimeType);
        } else {
          this.androidPermissions.requestPermissions([this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE, this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE])
            .then(status => {
              if (status.hasPermission) {
                this.openFile(location, fileMimeType);
              }
            });
        }
      });
  }

  saveFile(fileMimeType, location) {
    this.openFile(location, fileMimeType);
  }

  openFile(location, fileMimeType) {
    this.utils.dissmisProgressLoader();
    if (fileMimeType != 'application/zip') {
      this.fileOpener.open(location, fileMimeType)
        .then((res) => {
          this.downloadLogs();
        })
        .catch(err => {
          this.presentToast(this.translate.instant('save-photo.file_opening_failed'));
        });
    } else {
      this.presentToast(this.translate.instant('save-photo.file_saved_to_android') + this.file.externalDataDirectory + this.dirName);
    }
  }

  dialNumber(event: Event) {
    if (event && event.stopPropagation) {
      event.stopPropagation();
    }
  }

  openUrl = function () {
    this.utils.launchExternalApp('', '', '', 'https://asia-assistance.com/easy-locator');
  }

  downloadLogs() {
    this.eMedicalCardSrvc.getDownloadDetails(this.policyDetails['encryptedPolicyNo']).then((res) => {
      try {
        console.log('success');
      } catch (e) {
        this.logger.error("logSuccess catch eror : " + JSON.stringify(e));
        this.errorMsg = this.translate.instant('generic_msgs.server_err');
      }
    }, (err) => {
      this.errorMsg = this.utils.handleServiceException(err);
    });
  }
}
